import React from "react";
import TimeLine from "./TimeLine";

function Experience() {
  return (
    <section className="bg-light-gray" data-scroll-index="2">
      <div className="container">
        <div className="row">
          <div className="col-12 center-col margin-70px-bottom sm-margin-40px-bottom xs-margin-30px-bottom text-center">
            <h3 className="font-weight-700 font-size32 md-font-size27 sm-font-size24 xs-font-size20 section-title">
              My Experience
            </h3>
          </div>
        </div>
        <div className="row">
          <div className="col-md-12">
            <div className="main-timeline">
              <TimeLine />
            </div>
          </div>
        </div>
      </div>
    </section>
  );
}

export default Experience;
