import { TOGGLE_SIDEMENU, TOGGLE_NAV } from "../constants/action-types";
const initialState = {
  sidemenuOn: false,
  navToggleOn: "abc",
  experience: [
    { month: "1.3 Year", year: "2020", title: "SSE", description: "SE" },
    {
      month: "1 Year",
      year: "2018",
      title: "Associate Consultant",
      description: "SE"
    },
    { month: "1 Year", year: "2017", title: "SSE", description: "SE" },
    { month: "1.2 Year", year: "2016", title: "SE", description: "SE" }
  ]
};
const rootReducer = (state = initialState, action) => {
  switch (action.type) {
    case TOGGLE_SIDEMENU:
      return Object.assign({}, state, { sidemenuOn: !state.sidemenuOn });
    case TOGGLE_NAV:
      console.log("in reducer ", state);
      return Object.assign({}, state, { navToggleOn: state.navToggleOn });
    default:
      return state;
  }
};
export default rootReducer;
