import React from "react";
import { connect } from "react-redux";
const mapStateToProps = state => {
  return { experience: state.experience };
};
function TimeLine(props) {
  return props.experience.map((exp, k) => (
    <div className="timeline" key={k}>
      <div className="icon" />
      <div className="date-content">
        <div className="date-outer">
          <span className="date">
            <span className="month">{exp.month}</span>
            <span className="year">{exp.year}</span>
          </span>
        </div>
      </div>
      <div className="timeline-content">
        <h5 className="title">{exp.title}</h5>
        <p className="description">
          Lorem ipsum dolor sit amet, consectetur adipiscing elit. Sed efficitur
          ex sit amet massa scelerisque scelerisque. Aliquam erat volutpat.
          Aenean interdum finibus efficitur. Praesent dapibus dolor felis, eu
          ultrices elit molestie.
        </p>
      </div>
    </div>
  ));
}

export default connect(mapStateToProps)(TimeLine);
